import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    user: null
  },
  mutations: {
    setUserInfo (state, payload) {
      console.log(payload)
      state.user = payload
      window.localStorage.setItem('user', JSON.stringify(payload))
    }
  },
  actions: {
    setUserInfo (context) {
      context.commit('setUserInfo')
    }
  }
})

export default store



