<script>
export default {
  data () {
    return {
      isLoadingMixinFlag_: false
    }
  },
  computed: {
    isLoading: {
      get () {
        return this.isLoadingMixinFlag_
      },
      set (newValue) {
        this.isLoadingMixinFlag_ = newValue
        this.$bus.$emit('isLoading', newValue)
      }
    }
  }
}
</script>



