<template>
  <nav class="navbar navbar-expand-lg navbar-light bg-light">
    <router-link :to="{path:'/'}" class="navbar-brand font-weight-light logo" href="#">To Do List</router-link>
    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>

    <div class="collapse navbar-collapse" id="navbarSupportedContent">
      <ul class="navbar-nav ml-auto">
        <li class="nav-item">
          <router-link :to="{name: 'todoList'}" class="nav-link" href="#">
            <font-awesome-icon fixed-width class="ml-auto text-dark" icon="clipboard" />
            
          </router-link>
        </li>
        <li class="nav-item">
          <router-link :to="{path:'/googleCalendar'}" class="nav-link" href="#">
            <font-awesome-icon fixed-width class="ml-auto text-dark" icon="calendar-alt" />
            Google
          </router-link>
        </li>
        <li class="nav-item">
          <router-link :to="{path:'/test'}" class="nav-link" href="#">
            <font-awesome-icon fixed-width class="ml-auto text-dark" icon="star" />
            
          </router-link>
        </li>
        <li class="nav-item">
          <a class="nav-link" href="#" @click="logout()">
            <font-awesome-icon fixed-width class="ml-auto text-dark" icon="sign-out-alt" />
            
          </a>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import { rtdbDBAuth } from '@/service/rtdb_db'

export default {
  methods: {
    logout () {
      rtdbDBAuth.signOut()
        .then(() => {
          this.user = null
          this.isAuth = false
          console.log('you will logout')
        }).catch(err => console.error(err))
    }
  }
}
</script>



