<template>
  <div id="app">
    <app-header v-if="!currentPath"/>
    <div class="container-fluid p-3">
      <div class="row">
        <app-nav v-if="!currentPath" />
        <div v-if="!currentPath" class="col-md-9 col-sm-12">
          <router-view />
        </div>
        <div v-if="currentPath" class="col-md-12 col-sm-12">
          <router-view />
        </div>
      </div>
    </div>
    <!-- <app-footer /> -->
  </div>
</template>

<script>
import AppHeader from '@/components/template/app-header'
import AppNav from '@/components/template/app-nav'
import AppFooter from '@/components/template/app-footer'

export default {
  name: 'App',
  components: {
    AppHeader,
    AppNav,
    AppFooter
  },
  computed: {
    currentPath () {
      return this.$route.path === '/login'
    }
  }
}
</script>

<style lang='scss'>
@import './assets/scss/app';
</style>



