import Vue from 'vue'
import Router from 'vue-router'
import store from '@/service/store'

const routerList = [
  // 
  {
    path: '/login',
    name: 'login',
    component: () => import('@/components/login')
  },
  // 
  {
    path: '/',
    name: 'todoList',
    component: () => import('@/components/todo-list')
  }, {
    path: '/todoEdit/:id',
    name: 'todoEdit',
    component: () => import('@/components/todo-edit'),
    props: true
  }, {
    path: '/todoGroupList/:id',
    name: 'todoGroupList',
    component: () => import('@/components/todo-group-list'),
    props: true
  }, {
    path: '/nTodoGroupList/:id',
    name: 'nTodoGroupList',
    component: () => import('@/components/n-todo-group-list'),
    props: true
  }, {
    path: '/googleCalendar',
    name: 'googleCalendar',
    component: () => import('@/components/google-calendar')
  }, {
    path: '/',
    name: 'AppHeader',
    component: () => import('@/components/template/app-header')
  }, {
    path: '/',
    name: 'AppFooter',
    component: () => import('@/components/template/app-footer')
  }
]

Vue.use(Router)

const router = new Router({
  mode: 'history',
  linkActiveClass: 'active',
  routes: routerList
})

router.beforeEach(async (to, from, next) => {
  const user = localStorage.getItem('user')
  console.log(to, from)
  console.log(store)
  console.log(user)
  next()
})

export default router



